<template>
    <div>
        <h5>Ema</h5>
        <div class="d-flex flex-wrap" style="gap: 10px;">
            <div v-for="code in codes" :key="code.code" class="card" style="width: 25rem;">
                <div class="card-header text-info">{{ code.code }}</div>
                <div class="card-body text-dark">
                    <h6 class="card-subtitle mb-2 text-muted">{{ code.current }} / {{ code.percent }} (%)</h6>
                    <p class="card-text" v-bind:class="candles[code.candle]">Buy With Price : {{ code.price_ema }}</p>
                    <p class="card-text" v-bind:class="candles[code.candle]">Candle : {{ code.candle }}</p>
                    <p class="card-text" v-bind:class="clouds[code.cloud]">Cloud : {{ code.cloud }}</p>
                    <p class="card-text" v-bind:class="priceClouds[code.price_cloud]">Price Cloud : {{code.price_cloud}}</p>
                    <p class="card-text" v-bind:class="godEyes[code.god_eyes]">God Eyes : {{ code.god_eyes }}</p>
                    <p class="card-text" v-bind:class="volume(code.volume_target_percent)">Sale Rate : {{ code.sale_rate }} / {{ code.volume_target_percent }} (%)</p>
                    <p class="card-text">Rsi : {{ code.rsi }}</p>
                    <p class="card-text">History Line : {{ code.history_line }}</p>
                    <div class="overlay">
                        <div :id="'candle-'+code.code"></div>
                        <a :href="code.url" target="_blank" class="card-link">Chart</a>
                        <button v-if="code.type == types.STOCK" class="card-link btn btn-link" :id="'tooltip-info-'+code.code">
                            Information
                        </button>
                        <b-tooltip v-if="code.type == types.STOCK" :target="'tooltip-info-'+code.code" triggers="hover" placement="bottomright" boundary="document">
                            <ul class="container text-info text-lg-left">
                                <li>Roe : {{ code.basic.roe }}</li>
                                <li>EPS : {{ code.basic.eps }}</li>
                                <li>PE  : {{ code.basic.pe.toFixed(2) }}</li>
                                <li>Gross Margin  : {{ code.basic.gross_margin }}</li>
                                <li class="text-warning">Industry Roe  : {{ code.basic.industry_roe }}</li>
                                <li class="text-warning">Industry PE  : {{ code.basic.industry_pe }}</li>
                                <li class="text-warning">RI Name  : {{ code.root_industry_name }}</li>
                            </ul>
                        </b-tooltip>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Pusher from 'pusher-js'
import {CANDLES, CLOUDS, PRICE_CLOUDS, GOD_EYES, TYPES} from '@/common/config'
import ApexCharts from 'apexcharts'

export default {
    name: 'EmaIndex',
    watch: {
        codes(codes) {
            this.codes = codes
        },
    },
    header: {
        title: 'Candle',
        subTitle: 'Candle',
    },

    data() {
        return {
            codes: [],
            candles: CANDLES,
            clouds: CLOUDS,
            priceClouds: PRICE_CLOUDS,
            godEyes: GOD_EYES,
            types: TYPES,
            charts: [],
            candleSticks: {
                series: [
                    {
                        data: [],
                    },
                ],
                chart: {
                    type: 'candlestick',
                    height: 350,
                },
                xaxis: {
                    type: 'datetime',
                },
                yaxis: {
                    tooltip: {
                        enabled: true,
                    },
                },
            },
            chartsApex: [],
        }
    },
    updated: function () {
        this.$nextTick(function () {
            this.codes.forEach(code => {
                //check element
                let element = document.getElementById('candle-' + code.code)
                if (element != null) {
                    if (!this.chartsApex[code.code]) {
                        this.chartsApex[code.code] = new ApexCharts(document.getElementById('candle-' + code.code), this.charts[code.code])
                        this.chartsApex[code.code].render()
                    }
                }
            })
        })
    },
    mounted() {
        //Pusher.logToConsole = true

        let pusher = new Pusher('b704586710340d91ae1f', {
            cluster: 'eu',
            encrypted: false,
        })

        //Subscribe to the channel we specified in our Adonis Application
        let channel = pusher.subscribe('price')

        channel.bind('ema-price', (data) => {
            // if data in array codes , then update codes
            this.updateChart(data)

            if (this.codes.length > 0) {
                let index = this.codes.findIndex(code => code.code === data.code)
                if (index > -1) {
                    this.codes[index].current = data.current
                    this.codes[index].percent = data.percent
                    this.codes[index].candle = data.candle
                    this.codes[index].cloud = data.cloud
                    this.codes[index].sale_rate = data.sale_rate
                    this.codes[index].rsi = data.rsi
                    this.codes[index].history_line = data.history_line
                    this.codes[index].price_cloud = data.price_cloud
                    this.codes[index].god_eyes = data.god_eyes
                    this.codes[index].volume_target_percent = data.volume_target_percent

                    this.chartsApex[data.code].updateSeries(this.charts[data.code].series)
                } else {
                    this.codes.push(data)
                }
            } else {
                this.codes.push(data)
            }
        })
    },
    methods: {
        resetCandleSticks() {
            this.candleSticks.series[0].data = []
        },
        updateChart(data) {
            this.resetCandleSticks()
            let startTime = data.period_time[0]
            let fixed = this.countDecimals(data.current)

            fixed = fixed > 1 ? fixed : 2
            for (let i = 0; i < data.period.length; i++) {
                this.candleSticks.series[0].data.push({
                    x: startTime * 1000,
                    y: [
                        data.period_open[i].toFixed(fixed),
                        data.period_high[i].toFixed(fixed),
                        data.period_low[i].toFixed(fixed),
                        data.period[i].toFixed(fixed),
                    ],
                })
                startTime = startTime - 86400
            }
            this.charts[data.code] = this.candleSticks
        },
        volume(data) {
            if(data > 100) {
                return 'text-success'
            } else if(data > 50) {
                return 'text-warning'
            } else {
                return 'text-danger'
            }
        },
    },
}
</script>
<style>
.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: white;
}

.card:hover .overlay {
    opacity: 1;
}
</style>
