<template>
    <div>
        <div v-if="data.code" :key="code" class="card">
            <div class="card-header text-info">{{ code }}</div>
            <div class="card-body text-dark">
                <h5 class="card-title">{{ code }}</h5>
                <h6 class="card-subtitle mb-2 text-muted">{{ data.current }} / {{ data.percent }} (%)</h6>
                <p class="card-text">Buy With : {{ data.buy_right }}</p>
                <p class="card-text" v-bind:class="candles[data.candle]">Candle : {{ data.candle }}</p>
                <p class="card-text" v-bind:class="clouds[data.cloud]">Cloud : {{ data.cloud }}</p>
                <p class="card-text" v-bind:class="priceClouds[data.price_cloud]">Price Cloud : {{ data.price_cloud }}</p>
                <p class="card-text" v-bind:class="crossesEma[data.cross_ema]">Cross Ema : {{ data.cross_ema }}</p>
                <p class="card-text" v-bind:class="godEyes[data.god_eyes]">God Eyes : {{ data.god_eyes }}</p>
                <p class="card-text" v-bind:class="volume(data.volume_target_percent)">Sale Rate : {{ data.sale_rate }} / {{ data.volume_target_percent }} (%)</p>
                <p class="card-text">History Line : {{ data.history_line }}</p>
                <div class="overlay">
                    <div :id="'candle-'+code"></div>
                    <a :href="data.url" target="_blank" class="card-link">Chart</a>
                    <button v-if="data.type == types.STOCK" class="card-link btn btn-link" :id="'tooltip-info-'+code">
                        Information
                    </button>
                    <b-tooltip v-if="data.type == types.STOCK" :target="'tooltip-info-'+code" triggers="hover" placement="bottomright" boundary="document">
                        <ul class="container text-info text-lg-left">
                            <li>Roe : {{ data.basic.roe }}</li>
                            <li>EPS : {{ data.basic.eps }}</li>
                            <li>PE : {{ data.basic.pe.toFixed(2) }}</li>
                            <li>Gross Margin : {{ data.basic.gross_margin }}</li>
                            <li class="text-warning">Industry Roe : {{ data.basic.industry_roe }}</li>
                            <li class="text-warning">Industry PE : {{ data.basic.industry_pe }}</li>
                            <li class="text-warning">RI Name : {{ data.root_industry_name }}</li>
                        </ul>
                    </b-tooltip>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    CANDLES,
    CLOUDS,
    PRICE_CLOUDS,
    CROSSES_EMA,
    GOD_EYES,
    TYPES,
    GOOGLE_DRIVE_URL,
    GOOGLE_DRIVE_API_KEY,
} from '@/common/config'
// import ApexCharts from 'apexcharts'
import axios from 'axios'
import ApexCharts from 'apexcharts'

export default {
    name: 'CodeIndex',
    header: {
        title: 'Code Index',
        subTitle: 'Code',
    },

    data() {
        return {
            id: this.$route.query.id,
            code: this.$route.query.c,
            data: {},
            candles: CANDLES,
            clouds: CLOUDS,
            priceClouds: PRICE_CLOUDS,
            crossesEma: CROSSES_EMA,
            godEyes: GOD_EYES,
            types: TYPES,
            googleDriveUrl: GOOGLE_DRIVE_URL,
            googleDriveApiKey: GOOGLE_DRIVE_API_KEY,
            chart: [],
            candleSticks: {
                series: [
                    {
                        name: 'candle',
                        type: 'candlestick',
                        data: [],
                    },
                    {
                        name: 'maxLine',
                        type: 'line',
                        data: [],
                    },
                    {
                        name: 'minLine',
                        type: 'line',
                        data: [],
                    },
                ],
                chart: {
                    type: 'line',
                    height: 350,
                },
                xaxis: {
                    type: 'datetime',
                },
                yaxis: {
                    tooltip: {
                        enabled: true,
                    },
                },
                stroke: {
                    width: [1, 2, 2],
                },
            },
        }
    },
    updated() {
        new ApexCharts(document.getElementById('candle-' + this.code), this.chart).render()
    },
    mounted() {
        let url = this.googleDriveUrl + this.id + '?key=' + this.googleDriveApiKey + '&alt=media'
        axios.get(url).then(response => {
            this.data = response.data
            this.updateChart(this.data)
        }).catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
        })
    },
    methods: {
        countDecimals(value) {
            if (Math.floor(value) === value) {
                return 0
            }
            return value.toString().split('.')[1].length || 0
        },
        resetCandleSticks() {
            this.candleSticks.series[0].data = []
            this.candleSticks.series[1].data = []
            this.candleSticks.series[2].data = []
        },
        updateChart(data) {
            this.resetCandleSticks()
            let startTime = data.period_time[0] * 1000
            let fixed = this.countDecimals(data.current)

            fixed = fixed > 1 ? fixed : 2
            for (let i = 0; i < data.period.length; i++) {
                if (i === 0) {
                    this.candleSticks.series[1].data.push({
                        x: startTime,
                        y: data.buy_right ? data.buy_right.toFixed(fixed) : data.period[i],
                    })
                    this.candleSticks.series[2].data.push({
                        x: startTime,
                        y: data.sell_left ? data.sell_left.toFixed(fixed) : data.period[i],
                    })
                }
                if (i === data.triangle.line_max[0][0]) {
                    this.candleSticks.series[1].data.push({
                        x: startTime,
                        y: data.triangle.line_max[0][1].toFixed(fixed),
                    })
                }
                if (i === data.triangle.line_max[1][0]) {
                    this.candleSticks.series[1].data.push(
                        {
                            x: startTime,
                            y: data.triangle.line_max[1][1].toFixed(fixed),
                        })
                }
                if (i === data.triangle.line_min[0][0]) {
                    this.candleSticks.series[2].data.push({
                        x: startTime,
                        y: data.triangle.line_min[0][1].toFixed(fixed),
                    })
                }
                if (i === data.triangle.line_min[1][0]) {
                    this.candleSticks.series[2].data.push(
                        {
                            x: startTime,
                            y: data.triangle.line_min[1][1].toFixed(fixed),
                        })
                }
                this.candleSticks.series[0].data.push({
                    x: startTime,
                    y: [
                        data.period_open[i].toFixed(fixed),
                        data.period_high[i].toFixed(fixed),
                        data.period_low[i].toFixed(fixed),
                        data.period[i].toFixed(fixed),
                    ],
                })
                startTime = startTime - 86400
            }
            this.candleSticks.series[1].data.shift()
            this.candleSticks.series[2].data.shift()
            this.chart = this.candleSticks
        },
        volume(data) {
            if (data > 100) {
                return 'text-success'
            }
            else if (data > 50) {
                return 'text-warning'
            }
            else {
                return 'text-danger'
            }
        },
    },
}
</script>
<style>
.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: white;
}

.card:hover .overlay {
    opacity: 1;
}
</style>
