import Vue from 'vue'
import VueRouter from 'vue-router'
import Candle from '../views/Candle.vue'
import Ema from '../views/EmaIndex.vue'
import Triangle from '../views/Triangle.vue'
import Code from '../views/Code.vue'
import About from '../views/About.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'triangle_index',
        component: Triangle,
    },
    {
        path: '/ema',
        name: 'ema_index',
        component: Ema,
    },
    {
        path: '/candle',
        name: 'candle_index',
        component: Candle,
    },
    {
        path: '/code',
        name: 'code_index',
        component: Code,
    },
    {
        path: '*',
        name: 'not_found',
        component: About,
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
})

export default router
